.backdrop {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.9);
    z-index: 2;
}

.header {
    color: #333;
    text-align: center;
    margin: 0;
    margin-bottom: 30px;
    ;
}

.box {
    padding: 30px 15px;
    border: 1px solid #ccc;
    display: flex;
    width: 300px;
    margin: 30px auto;
    flex-direction: column;
    background: white;
    position: fixed;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 3;
    border-radius: 10px;
}

.label {
    font-size: 25px;
    font-weight: 600;
}

.input {
    padding: 10px;
    font-size: 18px;
    outline: none;
    border-radius: 5px;
    border: 1px solid #ccc;
    margin: 15px 0;
}

.btn {
    padding: 15px;
    border-radius: 10px;
    outline: none;
    background: green;
    color: white;
    font-size: 16px;
    cursor: pointer;
    border: 1px solid green;
    margin: 5px 0;
}

.btn:hover {
    color: black;
    background: white;
    ;
}