*{
  box-sizing: border-box;
}
body{
  background: lightsalmon;
}
.App {
  text-align: center;
  max-width:1350px;
  margin: 0 auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.btn_add{
  padding: 10px;
  border-radius: 5px;
  font-size: 16px;
  outline: none;
  cursor: pointer;
  border: 1px solid #000;
}
.btn_add:hover{
  background: #022;
  color:white;
}